export default "#iframe--opacity-listing-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * .listing-page\n      */\n\n    .listing-page_inner {\n      > .row {\n        .row-default();\n      }\n    }\n\n    .listing-page_col {\n      .col(12);\n      .col(6, sm);\n      .col(4, lg);\n    }\n\n    .listing-page_col--big {\n      .col(12, sm);\n      .col(12, lg);\n    }\n\n    .listing-page_item {\n      .full();\n      .text-black links-default() !important;\n      // card\n      .card-white();\n      .w-64();\n      .card-squared();\n      .card-none-x();\n      .card-title {\n        .font-sans-semibold();\n        font-size: 2rem;\n      }\n      .card-asset {\n        .media-container {\n          .padding(bottom, 75%);\n        }\n        .media {\n          .object-cover();\n        }\n      }\n    }\n\n    .listing-page_col--big {\n      .card-asset {\n        .media-container {\n          @media @min-sm {\n            .padding(bottom, 37.5%);\n          }\n          @media @min-lg {\n            .padding(bottom, 25%);\n          }\n        }\n      }\n    }\n\n  }\n  /*##END*/\n}\n";