export default ".demo--drop-animation {\n  @apply duration-500; /* needed for animation time, put max animation time */\n  > .drop {\n    @apply duration-500; /* needed for animation time, put max animation time */\n    /* initial */\n    > .drop-inner {\n      @apply transform transition-all duration-500;\n      @apply opacity-0 -translate-x-4;\n      .inverse& {\n        @apply translate-x-4;\n      }\n    }\n    /* in */\n    &.in {\n      > .drop-inner {\n        @apply opacity-100 translate-x-0 ease-in;\n      }\n    }\n    /* out */\n    &.out {\n      > .drop-inner {\n        @apply opacity-0 translate-x-4 ease-out;\n        .inverse& {\n          @apply -translate-x-4;\n        }\n      }\n    }\n  }\n}\n";