export default "#iframe--stores-listing-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * vars\n      */\n\n    // ease\n\n    @ease-in: @ease-out-quint;\n    @ease-out: @ease-out-quint;\n\n    /**\n      * .listing-page\n      */\n\n    .listing-page_inner {\n      > .row {\n        .row-default();\n        .row-stretch();\n      }\n    }\n\n    .listing-page_col {\n      .col(12);\n      .col(6, sm);\n      .col(4, lg);\n    }\n\n    .listing-page_item {\n      .full();\n      .text-black links-default() !important;\n      .btn-close {\n        .btn-squared();\n        .btn-primary();\n      }\n    }\n\n    .card-asset {\n      text-align: center;\n      .media-container {\n        .padding(bottom, 50%);\n      }\n      .media {\n        .object-cover();\n      }\n    }\n\n    .listing-page_subtitle {\n      .spacing-md();\n      .font-sans-semibold();\n      font-size: 2rem;\n      line-height: 1.2;\n      letter-spacing: 0;\n      text-transform: none;\n    }\n\n    .listing-page_address {\n      .full();\n      font-size: 1rem;\n      line-height: 1.8;\n    }\n\n    .listing-page_additional {\n      .full();\n      font-size: 0.9rem;\n      line-height: 1.8;\n    }\n\n    .listing-page_btns {\n      .list-block();\n      .list(tiny);\n      margin-top: auto;\n      .padding(top, medium);\n      .btn {\n        .full();\n        .align-center();\n        .btn-squared();\n      }\n    }\n\n    // front and back\n\n    .listing-page_front {\n      position: relative;\n      z-index: 2;\n      -webkit-backface-visibility: hidden;\n      backface-visibility: hidden;\n      // card\n      height: 100%;\n      .card-squared();\n      .text-black links-default();\n      .card-design {\n        border-color: @shade-100;\n        background: @shade-100;\n      }\n      .card-asset {\n        display: flex;\n        flex-direction: column;\n        .flex-full();\n        padding: 3.5rem;\n      }\n      .listing-page_btns {\n        margin-top: auto;\n      }\n    }\n\n    .listing-page_back {\n      position: absolute;\n      z-index: 1;\n      top: 0;\n      left: 0;\n      bottom: 0;\n      right: 0;\n      transform: rotateY(180deg);\n      -webkit-backface-visibility: hidden;\n      backface-visibility: hidden;\n      // card\n      .card-squared();\n      .text-black links-default();\n      .card-design {\n        border-color: @shade-100;\n        background: @shade-100;\n      }\n      .card-asset {\n        .flex-full();\n        padding: 3.5rem;\n      }\n    }\n\n    .listing-page_item {\n      perspective: 1400px;\n      .listing-page_item_inner {\n        height: 100%;\n        transform-style: preserve-3d;\n        transition-duration: @time-big;\n        transition-delay: initial;\n        transition-timing-function: @ease-out;\n      }\n      &.active {\n        .listing-page_item_inner {\n          transform: rotateY(180deg);\n          transition-duration: @time-big;\n          transition-delay: initial;\n          transition-timing-function: @ease-in;\n        }\n        .listing-page_back {\n          z-index: 3;\n        }\n      }\n    }\n\n  }\n  /*##END*/\n}\n";