export default "module.exports = {\n  variant: ['responsive'],\n  utility: {\n    '.drop-container-static': {\n      // setup\n      position: 'static',\n      '> .drop': {\n        width: '100%',\n        '> .drop-inner': {\n          width: '100%',\n        },\n        left: '0 !important',\n        right: '0 !important',\n      },\n    },\n    '.drop-container-disable': {\n      // setup\n      '@apply xt-disable': '',\n      position: 'initial',\n      '> a, > button': {\n        display: 'none',\n      },\n      '> .drop': {\n        display: 'inline-block !important',\n        position: 'relative',\n        top: 'auto',\n        left: 'auto',\n        right: 'auto',\n        bottom: 'auto',\n        '> .drop-inner': {\n          zIndex: 'initial',\n          '> .drop-design': {\n            display: 'none',\n          },\n          '> .card': {\n            '@apply card-disable !important': '',\n          },\n        },\n      },\n    },\n    '.drop-before': {\n      // setup\n      left: 'auto',\n      right: '100%',\n    },\n    '.drop-after': {\n      // setup\n      left: '100%',\n      right: 'auto',\n    },\n    '.drop-left': {\n      // setup\n      left: '0',\n      right: 'auto',\n      '&:before': {\n        display: 'none',\n        content: '\"\"',\n      },\n    },\n    '.drop-right': {\n      // setup\n      left: 'auto',\n      right: '0',\n      '&:before': {\n        display: 'none',\n        content: '\"\"',\n      },\n    },\n    '.drop-bottom': {\n      // setup\n      top: '100%',\n      bottom: 'auto',\n      '&:after': {\n        display: 'none',\n        content: '\"\"',\n      },\n    },\n    '.drop-top': {\n      // setup\n      top: 'auto',\n      bottom: '100%',\n      '&:after': {\n        display: 'none',\n        content: '\"\"',\n      },\n    },\n    '.drop-center': {\n      // setup\n      '&:before': {\n        display: 'none',\n        content: '\"xt-drop-center\"',\n      },\n    },\n    '.drop-middle': {\n      // setup\n      paddingTop: '0',\n      paddingBottom: '0',\n      '&:after': {\n        display: 'none',\n        content: '\"xt-drop-middle\"',\n      },\n    },\n    '.drop-contain-bottom': {\n      // setup\n      '@apply drop-middle': '',\n      top: 'auto',\n      bottom: '0',\n    },\n    '.drop-contain-top': {\n      // setup\n      '@apply drop-middle': '',\n      top: '0',\n      bottom: 'auto',\n    },\n  },\n  component: theme => ({\n    '.drop-container': {\n      // setup\n      position: 'relative',\n    },\n    '.drop': {\n      // setup\n      '@apply toggle': '',\n      position: 'absolute',\n      zIndex: 'inherit',\n      transitionDuration: 'inherit',\n      '&.in': {\n        zIndex: theme('zIndex.drop'),\n      },\n      '&.out': {\n        zIndex: theme('zIndex.drop') - 1,\n      },\n      // styles\n      '@apply drop-left drop-bottom': '',\n      padding: theme('spacing.2'),\n    },\n    '.drop-inner': {\n      // setup\n      position: 'relative',\n      zIndex: theme('zIndex.drop'),\n    },\n  }),\n}\n";