export default ".jump-to-content {\n  position: fixed !important;\n  left: 0;\n  width: 100%;\n  display: flex;\n  justify-content: center;\n  z-index: theme('zIndex.top');\n  /* initial */\n  @apply transform duration-500;\n  @apply opacity-0 -translate-y-4;\n  transition-property: opacity, transform;\n  /* in */\n  &.in {\n    @apply opacity-100 translate-y-0 ease-in;\n  }\n  /* out */\n  &.out {\n    @apply opacity-0 -translate-y-4 ease-out;\n  }\n}\n";