export default "#iframe--fashion-listing-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * .listing-page\n      */\n\n    .listing-page_inner {\n      > .row {\n        .row(large);\n      }\n    }\n\n    .listing-page_col {\n      .col(12);\n      .col(6, sm);\n      .col(3, lg);\n    }\n\n    .listing-page_col--big {\n      .col(12, sm);\n      .col(12, lg);\n    }\n\n    .listing-page_item {\n      .full();\n      // card\n      .card-white();\n      .card-squared();\n      .card-none-x();\n      text-align: center;\n      .card-title {\n        .font-sans-semibold();\n        font-size: 2rem;\n      }\n      .card-asset {\n        .media-container {\n          .padding(bottom, 150%);\n        }\n        .media {\n          .object-cover();\n        }\n      }\n    }\n\n    .listing-page_col--big {\n      .card-asset {\n        .media-container {\n          @media @min-sm {\n            .padding(bottom, 37.5%);\n          }\n          @media @min-lg {\n            .padding(bottom, 25%);\n          }\n        }\n      }\n    }\n\n    /**\n      * .media_mask\n      */\n\n    .media_mask {\n      position: absolute;\n      z-index: @index-active + 1;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 0;\n      background: @white;\n    }\n\n    /**\n      * scroll\n      */\n\n    // vars\n\n    @anim-scale-size: 1.1;\n\n    // the same css and js\n    .scroll-item, .listing-page_item {\n      opacity: 0;\n      &.fade {\n        opacity: 1;\n        animation-name: anim-fashion-listing-v1-in;\n        animation-duration: @time-md;\n        animation-timing-function: @ease-out-quint;\n        transform-origin: center top;\n        &.inverse {\n          opacity: 1;\n          animation-name: anim-fashion-listing-v1-inverse-in;\n          animation-duration: @time-md;\n          animation-timing-function: @ease-out-quint;\n          transform-origin: center bottom;\n        }\n      }\n      &.fade-out {\n        opacity: 0;\n        animation-name: anim-fashion-listing-v1-inverse-out;\n        animation-duration: @time-md;\n        animation-timing-function: @ease-out-quint;\n        transform-origin: center bottom;\n        &.inverse {\n          opacity: 0;\n          animation-name: anim-fashion-listing-v1-out;\n          animation-duration: @time-md;\n          animation-timing-function: @ease-out-quint;\n          transform-origin: center top;\n        }\n      }\n    }\n\n    // anim-fashion-listing-v1\n\n    @keyframes anim-fashion-listing-v1-in {\n      0% {\n        opacity: 0;\n        transform: translateY(5rem) scaleY(@anim-scale-size);\n      }\n      100% {\n        opacity: 1;\n        transform: translateY(0) scaleY(1);\n      }\n    }\n\n    @keyframes anim-fashion-listing-v1-out {\n      0% {\n        opacity: 1;\n        transform: translateY(0) scaleY(1);\n      }\n      100% {\n        opacity: 0;\n        transform: translateY(5rem) scaleY(@anim-scale-size);\n      }\n    }\n\n    // anim-fashion-listing-v1-inverse\n\n    @keyframes anim-fashion-listing-v1-inverse-in {\n      0% {\n        opacity: 0;\n        transform: translateY(-5rem) scaleY(@anim-scale-size);\n      }\n      100% {\n        opacity: 1;\n        transform: translateY(0) scaleY(1);\n      }\n    }\n\n    @keyframes anim-fashion-listing-v1-inverse-out {\n      0% {\n        opacity: 1;\n        transform: translateY(0) scaleY(1);\n      }\n      100% {\n        opacity: 0;\n        transform: translateY(-5rem) scaleY(@anim-scale-size)\n      }\n    }\n\n  }\n  /*##END*/\n}\n";