export default "#iframe--stores-listing-v2 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * .listing-page\n      */\n\n    // items\n\n    .listing-page_inner {\n      .padding(bottom, big);\n      > .row {\n        .row(small);\n        .row(medium, sm);\n      }\n    }\n\n    .listing-page_col {\n      .col(12);\n      .col(6, sm);\n      .col(4, lg);\n    }\n\n    .listing-page_item {\n      position: relative;\n      overflow: hidden;\n      @media @max-sm {\n        .listing-page_front {\n          .listing-page_item_content {\n            display: none;\n          }\n        }\n      }\n      @media @min-sm {\n        .listing-page_front {\n          display: block;\n          position: absolute;\n          top: 0;\n          left: 0;\n          width: 100%;\n          height: 100%;\n        }\n        .listing-page_back {\n          pointer-events: none;\n          opacity: 0;\n        }\n        &.active {\n          .listing-page_back {\n            pointer-events: all;\n          }\n        }\n      }\n      .listing-page_front {\n        cursor: pointer;\n        .card-squared();\n      }\n      .listing-page_back {\n        .card-squared();\n        min-height: 40rem;\n        .btn-close {\n          top: -0.7rem;\n          right: -0.7rem;\n        }\n        @media @max-sm {\n          .btn-close {\n            display: none;\n          }\n          .listing-page_content {\n            .padding(top, medium);\n          }\n        }\n      }\n    }\n\n    // content\n\n    .card-asset {\n      .margin(bottom, big);\n      .media-container {\n        height: 20rem;\n        @supports (mix-blend-mode: screen) {\n          &:before {\n            content: '';\n            position: absolute;\n            z-index: @index-active + 1;\n            top: 0;\n            left: 0;\n            right: 0;\n            bottom: 0;\n            background: #000000;\n            mix-blend-mode: saturation;\n          }\n          &:after {\n            content: '';\n            position: absolute;\n            z-index: @index-active + 2;\n            top: 0;\n            left: 0;\n            right: 0;\n            bottom: 0;\n            background: #7686FF;\n            mix-blend-mode: screen;\n            opacity: .25;\n          }\n        }\n      }\n      .media {\n        .object-cover();\n      }\n    }\n\n    .listing-page_content {\n      .padding(y, 0);\n      .padding(x, 4rem);\n      border-left: 1px solid @shade-200;\n    }\n\n    .listing-page_title {\n      .spacing-md();\n      .font-sans-semibold();\n      font-size: 3.0.7rem;\n      .line-height-mini();\n    }\n\n    .listing-page_address {\n      .line-height-mini();\n    }\n\n    .listing-page_subtitle {\n      .spacing-sm();\n      .font-sans-semibold();\n      font-size: 14px;\n      .line-height-mini();\n      text-transform: uppercase;\n    }\n\n    .listing-page_additional {\n      .font-sm();\n      .line-height-big();\n    }\n\n    .listing-page_btns {\n      display: none;\n    }\n\n  }\n  /*##END*/\n}\n";