export default ".demo--scroll-responsive {\n  .card {\n    @media (max-width: theme('screens.md')) {\n      /* mode-infinite */\n      /* initial */\n      @apply transform transition-all duration-500;\n      @apply opacity-0 -translate-x-4;\n      &.inverse {\n        @apply translate-x-4;\n      }\n      /* in */\n      &.fade-in {\n        @apply opacity-100 translate-x-0 ease-in;\n      }\n      /* out */\n      &.fade-out {\n        @apply opacity-0 translate-x-4 ease-out;\n        &.inverse {\n          @apply -translate-x-4;\n        }\n      }\n    }\n    @screen md {\n      /* mode-once */\n      &:not(.scroll-done) {\n        /* initial */\n        @apply transform transition-all duration-500;\n        @apply opacity-0 -translate-x-4;\n        &.inverse {\n          @apply translate-x-4;\n        }\n        /* in */\n        &.fade-in {\n          @apply opacity-100 translate-x-0 ease-in;\n        }\n        /* out */\n        &.fade-out {\n          @apply opacity-0 translate-x-4 ease-out;\n          &.inverse {\n            @apply -translate-x-4;\n          }\n        }\n      }\n    }\n  }\n}\n";