export default "const round = num =>\n  num\n    .toFixed(7)\n    .replace(/(\\.[0-9]+?)0+$/, '$1')\n    .replace(/\\.0$/, '')\nconst rem = px => `${round(px / 16)}rem`\n\nmodule.exports = {\n  variant: ['responsive'],\n  utility: {\n    '.card-group': {\n      // setup\n      display: 'flex',\n      alignItems: 'inherit',\n    },\n    '.card-disable': {\n      // setup\n      '@apply text-black links-default': '',\n      borderWidth: '0',\n      backgroundColor: 'transparent',\n      boxShadow: 'none',\n      '> .card-design': {\n        display: 'none',\n      },\n      '&, > *, > * > *': {\n        padding: '0',\n      },\n      '> .btn-close': {\n        display: 'none',\n      },\n    },\n  },\n  component: theme => ({\n    '.card': {\n      // setup\n      position: 'relative',\n      display: 'flex',\n      flexDirection: 'column',\n      alignItems: 'stretch',\n      justifyContent: 'flex-start',\n      flex: '1 1 auto',\n      width: '100%',\n      // animation\n      'a&, button&, label&': {\n        zIndex: theme('zIndex.base'),\n        transitionProperty: 'all',\n        transitionDuration: theme('transitionDuration.500'),\n        transitionTimingFunction: theme('transitionTimingFunction.out'),\n        '&:hover, &:active, &.active': {\n          zIndex: theme('zIndex.active'),\n          transitionTimingFunction: theme('transitionTimingFunction.in'),\n        },\n      },\n    },\n    '.card-sm': {\n      // styles\n      '@apply text-sm': '',\n      padding: `${rem(20)} ${rem(25)}`,\n    },\n    '.card-md': {\n      // styles\n      '@apply text-base': '',\n      padding: `${rem(30)} ${rem(35)}`,\n    },\n    '.card-lg': {\n      // styles\n      '@apply text-lg': '',\n      padding: `${rem(40)} ${rem(45)}`,\n    },\n  }),\n}\n";