export default "#iframe--xtend-theme-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * vars\n      */\n\n    // ease\n\n    @ease-in: @ease-out-quint;\n    @ease-out: @ease-in-out-quint;\n\n    /**\n      * button\n      */\n\n    // variant\n\n    .btn-default {\n      &:hover {\n        transform: translateY(-.25rem);\n        box-shadow: @shadow-light;\n      }\n      &:active, &.active {\n        transform: none;\n        box-shadow: @shadow-none;\n      }\n    }\n\n    .btn-primary {\n      &:hover {\n        transform: translateY(-.25rem);\n        box-shadow: @shadow-accent;\n      }\n      &:active, &.active {\n        transform: none;\n        box-shadow: @shadow-none;\n      }\n    }\n\n    // animation\n\n    .btn {\n      transition: all @time-tiny @ease-out;\n      &:hover {\n        transition: all @time-tiny @ease-in;\n      }\n      &:active, &.active {\n        transition: all @time-tiny @ease-in;\n      }\n    }\n\n    /**\n      * drop\n      */\n\n    // animation\n\n    .drop {\n      > .drop-inner {\n        > .drop-design {\n          opacity: 0;\n          transform: scale(.75);\n        }\n        > .drop-content {\n          opacity: 0;\n          transform: translateY(-1rem);\n        }\n      }\n      &.in {\n        animation-duration: @time-sm + @time-tiny; // needed for animation time, put max animation time\n        > .drop-inner {\n          > .drop-design {\n            opacity: 1;\n            transform: none;\n            transition: transform @time-md @ease-in, opacity @time-md @ease-in;\n          }\n          > .drop-content {\n            opacity: 1;\n            transform: none;\n            transition: transform @time-md @ease-in @time-tiny, opacity @time-md @ease-in @time-tiny;\n          }\n        }\n      }\n      &.out {\n        animation-duration: @time-sm + @time-tiny; // needed for animation time, put max animation time\n        > .drop-inner {\n          > .drop-design {\n            opacity: 0;\n            transform: scale(.75);\n            transition: transform @time-sm @ease-out @time-tiny, opacity @time-sm @ease-out @time-tiny;\n          }\n          > .drop-content {\n            opacity: 0;\n            transform: translateY(1rem);\n            transition: transform @time-sm @ease-out, opacity @time-sm @ease-out;\n          }\n        }\n      }\n    }\n\n    /**\n      * overlay\n      */\n\n    .overlay {\n      .card-item--menu {\n        background: @accent;\n      }\n    }\n\n    // animation\n\n    .overlay {\n      .card-overlay {\n        .card-design {\n          opacity: 0;\n          transform: scale(.75);\n        }\n        > .btn-close {\n          opacity: 0;\n        }\n        .card-block, .card-content {\n          opacity: 0;\n          transform: translateY(-1rem);\n        }\n      }\n      &.in {\n        animation-duration: @time-md + @time-tiny; // needed for animation time, put max animation time\n        .card-overlay {\n          .card-design {\n            opacity: 1;\n            transform: scale(1);\n            transition: transform @time-md @ease-in, opacity @time-md @ease-in;\n          }\n          > .btn-close {\n            // use animation to not override .btn transitions\n            .anim-zoom.in();\n            animation-delay: @time-md;\n            animation-fill-mode: forwards;\n            opacity: 0;\n          }\n          .card-block, .card-content {\n            opacity: 1;\n            transform: none;\n            transition: transform @time-md @ease-in @time-tiny, opacity @time-md @ease-in @time-tiny;\n          }\n        }\n      }\n      &.out {\n        animation-duration: @time-sm + @time-tiny; // needed for animation time, put max animation time\n        .card-overlay {\n          .card-design {\n            opacity: 0;\n            transform: scale(.75);\n            transition: transform @time-sm @ease-out @time-tiny, opacity @time-sm @ease-out @time-tiny;\n          }\n          > .btn-close {\n            // use animation to not override .btn transitions\n            .anim-zoom.out();\n            animation-delay: initial;\n          }\n          .card-block, .card-content {\n            opacity: 0;\n            transform: translateY(1rem);\n            transition: transform @time-sm @ease-out, opacity @time-sm @ease-out;\n          }\n        }\n      }\n    }\n\n    /**\n      * card interactive\n      */\n\n    // variant\n\n    a.card-default, label.card-default {\n      &:hover {\n        .card-design {\n          box-shadow: @shadow-light;\n        }\n      }\n      &:active, &.active {\n        .card-design {\n          box-shadow: none;\n        }\n      }\n    }\n\n    a.card-primary, label.card-primary {\n      &:hover {\n        .card-design {\n          box-shadow: @shadow-accent;\n        }\n      }\n      &:active, &.active {\n        .card-design {\n          box-shadow: none;\n        }\n      }\n    }\n\n    // animation\n\n    a.card, label.card {\n      transition: all @time-sm @ease-out;\n      .card-design {\n        transition: all @time-sm @ease-out;\n      }\n      &:hover {\n        transform: translateY(-.5rem);\n        transition: all @time-sm @ease-in;\n        .card-design {\n          transition: all @time-sm @ease-in;\n        }\n      }\n      &:active, &.active {\n        transform: none;\n        transition: all @time-sm @ease-in;\n        .card-design {\n          transition: all @time-sm @ease-in;\n        }\n      }\n    }\n\n  }\n  /*##END*/\n}\n";