export default "#iframe--slide-overlay-v1 {\n  /*##START*/\n  @import 'xtendui/src/addons/overlay-close-outside.less';\n\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * vars\n      */\n\n    // ease\n\n    @ease-in: @ease-out-quint;\n    @ease-out: @ease-in-out-quint;\n\n    /**\n      * overlay\n      */\n\n    .overlay {\n      .card-item--menu {\n        background: @accent;\n      }\n    }\n\n    // animation\n\n    .overlay {\n      .overlay-design {\n        opacity: 0;\n        transform: translateY(125%);\n      }\n      .card-overlay {\n        .card-block, .card-content {\n          opacity: 0;\n          transform: translateY(100%);\n        }\n      }\n      &.in {\n        animation-duration: @time-md; // needed for animation time, put max animation time\n        .overlay-design {\n          opacity: 1;\n          transform: none;\n          transition: transform @time-md @ease-in, opacity @time-sm @ease-in;\n        }\n        .card-overlay {\n          .card-block, .card-content {\n            opacity: 1;\n            transform: none;\n            transition: transform @time-md @ease-in, opacity @time-sm @ease-in;\n          }\n        }\n      }\n      &.out {\n        animation-duration: @time-sm; // needed for animation time, put max animation time\n        .overlay-design {\n          opacity: 0;\n          transform: translateY(125%);\n          transition: transform @time-sm @ease-out, opacity @time-sm @ease-out;\n        }\n        .card-overlay {\n          .card-block, .card-content {\n            opacity: 0;\n            transform: translateY(100%);\n            transition: transform @time-sm @ease-out, opacity @time-sm @ease-out;\n          }\n          > .btn-close {\n            .anim-y.out();\n          }\n        }\n      }\n    }\n\n    /**\n      * overlay position\n      */\n\n    // animation\n\n    [class^='overlay-position-'], [class*=' overlay-position-'] {\n      &.overlay-position-left {\n        .overlay-design {\n          opacity: 0;\n          transform: translateX(-125%);\n        }\n        .card-overlay {\n          .card-block, .card-content {\n            opacity: 0;\n            transform: translateX(-100%);\n          }\n        }\n      }\n      &.overlay-position-right {\n        .overlay-design {\n          opacity: 0;\n          transform: translateX(125%);\n        }\n        .card-overlay {\n          .card-block, .card-content {\n            opacity: 0;\n            transform: translateX(100%);\n          }\n        }\n      }\n      &.in {\n        animation-duration: @time-md; // needed for animation time, put max animation time\n        .overlay-design {\n          opacity: 1;\n          transform: none;\n          transition: transform @time-md @ease-in, opacity @time-sm @ease-in;\n        }\n        .card-overlay {\n          .card-block, .card-content {\n            opacity: 1;\n            transform: none;\n            transition: transform @time-md @ease-in, opacity @time-sm @ease-in;\n          }\n        }\n      }\n      &.out {\n        animation-duration: @time-sm; // needed for animation time, put max animation time\n        &.overlay-position-left {\n          .overlay-design {\n            opacity: 0;\n            transform: translateX(-125%);\n            transition: transform @time-sm @ease-out, opacity @time-sm @ease-out;\n          }\n          .card-overlay {\n            .card-block, .card-content {\n              opacity: 0;\n              transform: translateX(-100%);\n              transition: transform @time-sm @ease-out, opacity @time-sm @ease-out;\n            }\n            > .btn-close {\n              .anim-x-inverse.out();\n            }\n          }\n        }\n        &.overlay-position-right {\n          .overlay-design {\n            opacity: 0;\n            transform: translateX(125%);\n            transition: transform @time-sm @ease-out, opacity @time-sm @ease-out ;\n          }\n          .card-overlay {\n            .card-block, .card-content {\n              opacity: 0;\n              transform: translateX(100%);\n              transition: transform @time-sm @ease-out, opacity @time-sm @ease-out ;\n            }\n            > .btn-close {\n              .anim-x.out();\n            }\n          }\n        }\n      }\n    }\n\n  }\n  /*##END*/\n}\n";