export default "/**\n * footer\n */\n\n.gatsby_site_footer_inner {\n  @apply py-5;\n}\n\n.gatsby_site_footer_bottom {\n  a {\n    font-weight: theme('fontWeight.semibold');\n    color: theme('colors.black');\n    text-decoration: none;\n    &:hover, &:active, &.active {\n      color: theme('colors.black');\n      text-decoration: underline;\n    }\n  }\n  opacity: 0.5;\n  transition-property: opacity;\n  transition-duration: theme('transitionDuration.500');\n  transition-timing-function: theme('transitionTimingFunction.out');\n  &:hover {\n    opacity: 1;\n    transition-timing-function: theme('transitionTimingFunction.in');\n  }\n}\n\n.gatsby_site_footer_bottom_row {\n  flex-wrap: nowrap;\n  justify-content: space-between;\n  flex: 1 1 auto;\n  font-size: 11px;\n  @screen md {\n    font-size: 12px;\n  }\n  @screen lg {\n    font-size: 13px;\n  }\n}\n\n.gatsby_site_footer_bottom_right {\n  @screen md {\n    margin-left: auto;\n  }\n}\n";