export default "#iframe--infinite-scroll {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    .infinite-scroll--spinner {\n      opacity: 0;\n      transition-property: opacity;\n      transition-duration: theme('transitionDuration.500');\n      transition-timing-function: theme('transitionTimingFunction.out');\n      .spinner {\n        opacity: 0;\n        transform: scale(.75);\n        transition-property: all;\n        transition-duration: theme('transitionDuration.500');\n        transition-timing-function: theme('transitionTimingFunction.out');\n      }\n    }\n\n    .infinite-scroll-loading {\n      .infinite-scroll--spinner {\n        opacity: 1;\n        transition-timing-function: theme('transitionTimingFunction.in');\n        .spinner {\n          opacity: 1;\n          transform: scale(1);\n          transition-timing-function: theme('transitionTimingFunction.in');\n        }\n      }\n    }\n\n    [data-xt-infinite-scroll-trigger] {\n      .infinite-scroll--trigger_content-nomore {\n        display: none;\n      }\n      .infinite-scroll--trigger_content {\n        transition-property: transform, opacity;\n        transition-duration: theme('transitionDuration.500');\n        transition-timing-function: theme('transitionTimingFunction.out');\n      }\n    }\n\n    .infinite-scroll-loading {\n      [data-xt-infinite-scroll-trigger] {\n        pointer-events: none;\n        > .infinite-scroll--trigger_content {\n          opacity: 0;\n          transform: translateY(0.7rem);\n          transition-timing-function: theme('transitionTimingFunction.in');\n        }\n      }\n    }\n\n    .infinite-scroll-nomore {\n      [data-xt-infinite-scroll-trigger] {\n        pointer-events: none;\n        .infinite-scroll--trigger_content {\n          display: none;\n        }\n        .infinite-scroll--trigger_content-nomore {\n          display: block;\n        }\n      }\n    }\n\n    .infinite-scroll-pre {\n      display: none;\n    }\n\n    .infinite-scroll-reset {\n      .infinite-scroll-pre {\n        display: block;\n      }\n    }\n\n  }\n  /*##END*/\n}\n";