export default "#iframe--sequential-megamenu-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * .megamenus\n      */\n\n    .megamenus {\n      display: flex;\n      position: relative;\n    }\n\n    .megamenus_line {\n      position: fixed;\n      left: 0;\n      top: 0;\n      background: @shade-700;\n    }\n\n    .btn_megamenus {\n      .btn-lg();\n      .padding(y, 1.5em);\n    }\n\n    .megamenu_outer {\n      > .drop {\n        .drop-nospace();\n        .drop-squared();\n        > .drop-inner {\n          .text-white links-inverse();\n          > .drop-design {\n            border-color: @shade-700;\n            background: @shade-700;\n            box-shadow: @shadow-dark;\n          }\n        }\n      }\n      // animation\n      > .drop {\n        > .drop-inner {\n          overflow: hidden;\n          .transition-none() !important;\n          > .drop-design {\n            opacity: 1;\n          }\n          > .drop-content {\n            opacity: 1;\n          }\n        }\n      }\n      &.in {\n        > .drop {\n          > .drop-inner {\n            .transition-none() !important;\n            > .drop-design {\n              opacity: 1;\n              .transition-none();\n            }\n            > .drop-content {\n              opacity: 1;\n              .transition-none();\n            }\n          }\n        }\n      }\n      &.out {\n        > .drop {\n          > .drop-inner {\n            .transition-none() !important;\n            > .drop-design {\n              opacity: 1;\n              .transition-none();\n            }\n            > .drop-content {\n              opacity: 1;\n              .transition-none();\n            }\n          }\n        }\n      }\n    }\n\n    .megamenu {\n      .padding(y, 6rem);\n    }\n\n    .megamenu_inner {\n      > .row {\n        .row(medium);\n        .row(big, lg);\n      }\n    }\n\n    .megamenu_col_left {\n      .col(12);\n      .col(4, sm);\n      .col(3, lg);\n      padding-right: 4rem !important;\n    }\n\n    .megamenu_col_right {\n      .col(12);\n      .col(8, sm);\n      .col(9, lg);\n      > .row {\n        .row-y(small);\n        .row-x(medium);\n        .row-x(big, lg);\n        // max-height set in style\n        flex-direction: column;\n        flex-wrap: wrap;\n      }\n    }\n\n    .megamenu_item_col {\n      .col(6);\n      .col(4, sm);\n    }\n\n    .megamenu_item_title {\n      .link-none();\n      .spacing-tiny();\n      .font-sans-semibold();\n      .line-height-md();\n      font-size: 1rem;\n      color: @white;\n      letter-spacing: .025em;\n    }\n\n    .megamenu_item_content {\n      > .list-block {\n        .list(none);\n      }\n      a {\n        .link-none();\n        .padding(y, .25rem);\n        .font-sans-semibold();\n        .line-height-md();\n        font-size: 13px;\n        color: fade(@white, 50%) !important;\n        &:hover, &:active, &.active {\n          color: @white !important;\n        }\n      }\n    }\n\n  }\n  /*##END*/\n}\n";