export default "/**\n * structure\n */\n\nbody {\n  @apply bg-white;\n}\n\n/* indexes */\n\n.gatsby_site_main {\n  position: relative;\n  z-index: 5;\n}\n\n.gatsby_site_article_hero {\n  position: relative;\n  z-index: 6;\n}\n\n.gatsby_site_article_content {\n  position: relative;\n  z-index: 9;\n}\n\n.gatsby_site_footer {\n  position: relative;\n  z-index: 7;\n}\n\n/* spacing and background */\n\n.gatsby_site_article_hero {\n  @apply pt-20 pb-16;\n  border-bottom: 1px solid theme('colors.gray.200');\n  background: theme('colors.gray.100');\n  background: linear-gradient(225deg, theme('colors.gray.100') 33%, theme('colors.gray.200') 100%);\n}\n\n.gatsby_site_article_content {\n  @apply py-16;\n  flex: 1 1 auto;\n  background: theme('colors.white');\n}\n\n.gatsby_site_footer {\n  border-top: 1px solid theme('colors.gray.200');\n  background: theme('colors.gray.100');\n  background: linear-gradient(225deg, theme('colors.gray.100') 33%, theme('colors.gray.200') 100%);\n}\n\n/* container */\n\n.gatsby_site_article_hero_inner, .gatsby_site_article_content, .gatsby_site_footer_inner {\n  @apply container ml-0;\n  @screen xl {\n    max-width: 1152px;\n  }\n}\n\n/* prevent horizontal overflow */\n\n.gatsby_site_wrapper {\n  overflow: hidden;\n}\n\n.gatsby_site_header, .gatsby_site_main, .gatsby_site_footer {\n  @apply w-full;\n}\n\n/* sticky footer */\n\n.gatsby_site_wrapper {\n  display: flex;\n  flex-wrap: wrap;\n  flex-direction: column;\n  min-height: 100vh;\n  min-height: calc(var(--vh, 1vh) * 100);\n}\n\n.gatsby_site_main,\n.gatsby_site_main_inner,\n.gatsby_site_article,\n.gatsby_site_article_inner {\n  display: flex;\n  flex-direction: column;\n  flex: 1 1 auto;\n}\n\n/**\n * article\n */\n\n.gatsby_site_article {\n  @apply bg-white;\n  @screen lg {\n    margin-left: 18rem;\n  }\n  @screen xl {\n    margin-left: 20rem;\n  }\n}\n.gatsby_site_article_hero {\n  .p {\n    @apply -mt-2 mb-2 text-lg;\n    line-height: theme('lineHeight.snug');\n    opacity: 0.5;\n  }\n}\n\n.gatsby_site_article_hero_inner {\n  position: relative;\n}\n\n.gatsby_site_article_hero_content {\n  .badge {\n    @apply border-gray-300 bg-gray-300 -mt-4 ml-4;\n  }\n  h1 {\n    span {\n      display: inline-block;\n    }\n    span span {\n      font-weight: theme('fontWeight.normal');\n      color: theme('colors.gray.500');\n    }\n  }\n}\n\n.gatsby_site_article_hero_content_inner {\n  max-width: 70rem;\n  &:not(.fade) {\n     pointer-events: none;\n   }\n}\n\n/**\n * listing\n*/\n\n.gatsby_listing {\n  @apply mt-16 mt-fc;\n  div:empty + & {\n    @apply mt-0;\n  }\n}\n\n.gatsby_listing_group {\n  @apply w-full;\n}\n\n.gatsby_listing_items {\n  .gatsby_listing .h-block + & {\n    @apply my-10;\n  }\n  .gatsby_listing_group:last-child & {\n    @apply mb-0;\n  }\n  > .row {\n    @media (max-width: theme('screens.sm')) {\n      @apply row-x-1;\n      @apply -m-4;\n    }\n    @screen sm {\n      @apply -m-6;\n    }\n    .gatsby_site_article_foot & {\n      @apply mt-0 mb-4;\n    }\n  }\n}\n\n.gatsby_listing_column {\n  @apply w-2/4 md:w-2/6 lg:w-2/4 xl:w-2/6;\n  .gatsby_site_article_foot & {\n    &:nth-child(1) {\n      display: none;\n      @screen md {\n        display: block;\n      }\n    }\n  }\n}\n\n.gatsby_listing_item {\n  @apply py-4 px-5 text-xs text-black links-default rounded-md transform hover:text-white hover:bg-accent-500 hover:shadow-xl hover:-translate-y-2 active:text-white active:bg-accent-600 active:shadow-md active:-translate-y-1;\n  .h4 {\n    @apply mb-1.5 mt-fc mb-lc;\n    font-size: 1rem;\n  }\n  p {\n    opacity: 0.5;\n  }\n}\n";