export default "module.exports = {\n  utility: theme => ({\n    '.overlay-screen': {\n      // setup\n      '.overlay-container': {\n        position: 'relative',\n        zIndex: theme('zIndex.overlay'),\n        padding: '0 !important',\n        '.overlay-inner': {\n          position: 'static',\n        },\n      },\n      '.overlay-inner': {\n        display: 'flex',\n        flexDirection: 'column',\n        flex: '1 1 auto',\n      },\n    },\n    '.overlay-disable': {\n      // setup\n      '@apply xt-disable': '',\n      display: 'flex !important',\n      overflow: 'initial !important',\n      position: 'static',\n      zIndex: 'initial',\n      top: 'auto',\n      left: 'auto',\n      bottom: 'auto',\n      right: 'auto',\n      width: 'auto',\n      height: 'auto',\n      '.overlay-design': {\n        display: 'none',\n      },\n      '.overlay-container': {\n        zIndex: 'initial',\n        padding: '0',\n        width: '100%',\n        minHeight: 'auto',\n        maxWidth: 'none',\n      },\n      '.overlay-inner': {\n        '> .card': {\n          '@apply card-disable !important': '',\n        },\n      },\n      '> .backdrop': {\n        display: 'none !important',\n      },\n    },\n  }),\n  component: theme => ({\n    '.overlay': {\n      // setup\n      '@apply toggle overflow-main': '',\n      position: 'fixed',\n      zIndex: theme('zIndex.overlay'),\n      top: '0',\n      left: '0',\n      bottom: '0', // @FIX no height or it bugs collapse animation\n      right: '0', // @FIX no width or it bugs collapse animation\n      overflow: 'hidden',\n    },\n    '.overlay-container': {\n      // setup\n      display: 'flex',\n      flexWrap: 'nowrap',\n      flexDirection: 'column',\n      justifyContent: 'center',\n      margin: 'auto', // @FIX http://stackoverflow.com/questions/33454533/cant-scroll-to-top-of-flex-item-that-is-overflowing-container\n      width: '100%',\n      minHeight: '100vh',\n      padding: theme('container.padding.default'),\n      '@screen sm': {\n        padding: theme('container.padding.sm'),\n      },\n      '@screen md': {\n        padding: theme('container.padding.md'),\n      },\n      '@screen lg': {\n        padding: theme('container.padding.lg'),\n      },\n      '@screen xl': {\n        padding: theme('container.padding.xl'),\n      },\n    },\n    '.overlay-inner': {\n      // setup\n      display: 'flex',\n      justifyContent: 'center',\n      alignItems: 'center',\n      alignSelf: 'center',\n      position: 'relative',\n      zIndex: theme('zIndex.overlay'),\n      width: '100%',\n      borderRadius: 'inherit',\n    },\n  }),\n}\n";