export default "#iframe--fashion-gallery-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n     * product-page\n     */\n\n    .product-page {\n      .margin(bottom, 10rem);\n      @media @min-md {\n        .margin(top, large);\n      }\n    }\n\n    .product-page_toolbar {\n      .margin(bottom, mini);\n      .margin(bottom, small, sm);\n      > .list {\n        .list(medium);\n        align-items: center;\n        justify-content: space-between;\n      }\n      @media @max-md {\n        display: none;\n      }\n    }\n\n    .product-page_inner {\n      > .row {\n        @media @min-md {\n          .row(big);\n        }\n      }\n    }\n\n    .product-page_left_col {\n      .col(12);\n      .col(6, md);\n      .col(7, lg);\n    }\n\n    .product-page_right_col {\n      .col(12);\n      .col(6, md);\n      .col(5, lg);\n    }\n\n    .product-page_left {\n      @media @max-md {\n        overflow-x: hidden;\n        overflow-y: auto;\n        height: 60vh;\n        height: calc(var(--vh, 1vh) * 60);\n        .overflow-style(@mode: inner);\n      }\n    }\n\n    .product-page_right {\n      .padding(top, large);\n      @media @min-md {\n        .padding(top, giant);\n      }\n    }\n\n    .product-page_title {\n      .h1();\n      .margin(top, small);\n      .margin(bottom, small);\n      .margin(y, layout);\n    }\n\n    .product-page_price {\n      .margin(top, small);\n      .margin(bottom, small);\n      .margin(y, layout);\n      .font-sans-semibold();\n      .line-height-mini();\n      font-size: 3rem;\n      letter-spacing: 0;\n      text-transform: none;\n    }\n\n    .product-page_actions {\n      .margin(top, large);\n      .margin(bottom, large);\n      .margin(y, layout);\n    }\n\n    .product-page_details {\n      .margin(top, small);\n      .margin(bottom, small);\n      .margin(y, layout);\n      .font-big();\n    }\n\n    .product-page_attributes {\n      .margin(top, small);\n      .margin(bottom, small);\n      .margin(y, layout);\n      .font-big();\n    }\n\n    // images\n\n    .product-page_images {\n      .list-block {\n        .list(none);\n      }\n    }\n\n    .product-page_image {\n      display: block;\n      .full();\n      @media @max-md {\n        .media-container {\n          pointer-events: none;\n          .transform-none() !important;\n        }\n        .media-inner {\n          .transform-none() !important;\n        }\n        .media_mask {\n          display: none;\n        }\n      }\n    }\n\n    .media_mask {\n      position: absolute;\n      z-index: @index-active + 1;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 0;\n      background: @white;\n    }\n\n    #overlay--product-page_images {\n      .list-block {\n        .list(none);\n      }\n      img {\n        .full();\n      }\n    }\n\n    .product-page_left_outer {\n      position: relative;\n      .container-space-remove();\n      @media @min-md {\n        .margin(all, none) !important;\n      }\n    }\n\n    .product-page_images_nav {\n      @media @min-md {\n        display: none;\n      }\n      position: absolute;\n      top: 0;\n      right: 0;\n      height: 100%;\n      > .list-block {\n        height: 100%;\n        justify-content: center;\n      }\n    }\n\n    .product-page_images_nav_item {\n      .padding(y, .5rem);\n      .padding(x, 1rem);\n      .product-page_images_nav_item_inner {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        width: 9px;\n        height: 9px;\n      }\n      span {\n        display: inline-block;\n        width: 7px;\n        height: 7px;\n        border-radius: 50%;\n        border: 2px solid transparent;\n        background: @shade-700;\n        transition: all @time-sm @ease-in-out-expo;\n      }\n      &:active, &.active {\n        span {\n          width: 10px;\n          height: 10px;\n          border: 2px solid @shade-700;\n          background: transparent;\n          transition: all @time-sm @ease-in-out-expo;\n        }\n      }\n    }\n\n    // continue\n\n    .product-page_continue {\n      @media @min-md {\n        display: none;\n      }\n      .anim-y.in();\n      position: fixed;\n      z-index: @index-sticky;\n      bottom: 0;\n      left: 0;\n      width: 100%;\n      .align-center();\n      .padding(all, medium);\n      .padding(bottom, small);\n      font-size: 3rem;\n      color: @black;\n      background: linear-gradient(0deg, @white 0%, @white 50%, fade(@white, 0%) 100%);\n      .icon-caret-down {\n        display: inline-block;\n      }\n      &.scrolled {\n        .anim-y.out();\n        pointer-events: none;\n      }\n    }\n\n  }\n  /*##END*/\n}\n";