export default "module.exports = {\n  component: theme => ({\n    '.media-container': {\n      // setup\n      display: 'block',\n      position: 'relative',\n      overflow: 'hidden',\n      width: '100%',\n      minHeight: '100%',\n      borderRadius: 'inherit',\n      // styles\n      backgroundColor: theme('colors.gray.500'),\n    },\n    '.media-inner': {\n      // setup\n      position: 'absolute',\n      top: '0',\n      left: '0',\n      bottom: '0',\n      right: '0',\n    },\n    '.media': {\n      // setup\n      width: '100%',\n      height: '100%',\n    },\n  }),\n}\n";