export default "#iframe--collapse-theme-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * vars\n      */\n\n    // ease\n\n    @ease-in: @ease-out-quint;\n    @ease-out: @ease-in-out-quint;\n\n    /**\n      * drop\n      */\n\n    .drop-default {\n      > .drop-inner {\n        box-shadow: @shadow-light;\n        > .drop-design {\n          box-shadow: none;\n        }\n      }\n    }\n\n    .drop-primary {\n      > .drop-inner {\n        box-shadow: @shadow-dark;\n        > .drop-design {\n          box-shadow: none;\n        }\n      }\n    }\n\n    // animation\n\n  .drop {\n    > .drop-inner {\n      .collapse-height();\n      transition: height @time-md @ease-in;\n        > .drop-design {\n          opacity: 0;\n          transform: translateY(-0.7rem);\n        }\n        > .drop-content {\n          opacity: 0;\n          transform: translateY(-1rem);\n        }\n      }\n      &.in {\n        animation-duration: @time-md; // needed for animation time, put max animation time\n        > .drop-inner {\n          transition: height @time-md @ease-in;\n          > .drop-design {\n            opacity: 1;\n            transform: none;\n            transition: height @time-md @ease-in, transform @time-md @ease-in, opacity @time-tiny @ease-in;\n          }\n          > .drop-content {\n            opacity: 1;\n            transform: none;\n            transition: transform @time-md @ease-in, opacity @time-md @ease-in;\n          }\n        }\n      }\n      &.out {\n        animation-duration: @time-sm; // needed for animation time, put max animation time\n        > .drop-inner {\n          transition: height @time-sm @ease-out;\n          > .drop-design {\n            opacity: 0;\n            transform: translateY(0.7rem);\n            transition: height @time-sm @ease-out, transform @time-sm @ease-out, opacity @time-tiny @ease-out @time-tiny;\n          }\n          > .drop-content {\n            opacity: 0;\n            transform: translateY(1rem);\n            transition: transform @time-sm @ease-out, opacity @time-sm @ease-out;\n          }\n        }\n      }\n    }\n\n    /**\n      * overlay\n      */\n\n    .overlay {\n      .card-item--menu {\n        background: @accent;\n      }\n    }\n\n    // animation\n\n    .overlay {\n      .collapse-height();\n      .overlay-design {\n        opacity: 0;\n        transform: translateY(-2rem);\n      }\n      .card-overlay {\n        > .btn-close {\n          opacity: 0;\n        }\n        .card-block, .card-content {\n          opacity: 0;\n          transform: translateY(-2.5rem);\n        }\n      }\n      &.in {\n        animation-duration: @time-md; // needed for animation time, put max animation time\n        transition: height @time-md @ease-in;\n        .overlay-design {\n          opacity: 1;\n          transform: none;\n          transition: transform @time-md @ease-in, opacity @time-tiny @ease-in;\n        }\n        .card-overlay {\n          > .btn-close {\n            // use animation to not override .btn transitions\n            .anim-zoom.in();\n            animation-delay: @time-md;\n            animation-fill-mode: forwards;\n            opacity: 0;\n          }\n          .card-block, .card-content {\n            opacity: 1;\n            transform: none;\n            transition: transform @time-md @ease-in, opacity @time-tiny @ease-in;\n          }\n        }\n      }\n      &.out {\n        animation-duration: @time-sm; // needed for animation time, put max animation time\n        transition: height @time-sm @ease-out;\n        .overlay-design {\n          opacity: 0;\n          transform: translateY(2rem);\n          transition: transform @time-sm @ease-out, opacity @time-tiny @ease-out @time-tiny;\n        }\n        .card-overlay {\n          > .btn-close {\n            // use animation to not override .btn transitions\n            .anim-zoom.out();\n            animation-delay: initial;\n          }\n          .card-block, .card-content {\n            opacity: 0;\n            transform: translateY(2.5rem);\n            transition: transform @time-sm @ease-out, opacity @time-tiny @ease-out @time-tiny;\n          }\n        }\n      }\n    }\n\n    /**\n      * group\n      */\n\n    .group {\n      .collapse-width();\n      &.in {\n        transition: width @time-md @ease-out;\n      }\n      &.out {\n        transition: width @time-md @ease-out;\n      }\n    }\n\n  }\n  /*##END*/\n}\n";