export default "#iframe--furniture-listing-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * .listing-page\n      */\n\n    .listing-page_inner {\n      > .row {\n        .row(small);\n        .row(medium, sm);\n      }\n    }\n\n    .listing-page_col {\n      .col(12);\n      .col(6, sm);\n      .col(4, lg);\n    }\n\n    .listing-page_col--big {\n      .col(12, sm);\n      .col(12, lg);\n    }\n\n    .listing-page_item {\n      .full();\n      .text-black links-default() !important;\n      // card\n      .card-white();\n      .card-squared();\n      .card-none-x();\n      .card-title {\n        .font-sans-semibold();\n        .line-height-sm();\n        font-size: 1.2rem;\n      }\n      .card-asset {\n        .media-container {\n          .padding(bottom, 75%);\n          background: @shade-100;\n        }\n        .media {\n          .object-cover();\n        }\n      }\n    }\n\n    .listing-page_col--big {\n      .card-asset {\n        .media-container {\n          @media @min-sm {\n            .padding(bottom, 37.5%);\n          }\n          @media @min-lg {\n            .padding(bottom, 25%);\n          }\n        }\n      }\n    }\n\n    .listing-page_content {\n      .padding(y, medium);\n    }\n\n    .listing-page_price {\n      .font-sans-semibold();\n      .line-height-sm();\n      font-size: 0.9rem;\n      letter-spacing: 0.1em;\n      text-transform: none;\n    }\n\n    /**\n      * scroll\n      */\n\n    // vars\n\n    @anim-scale-size: 1.05;\n\n    // the same css and js\n    .scroll-item, .listing-page_item {\n      opacity: 0;\n      &.fade {\n        opacity: 1;\n        animation-name: anim-furniture-listing-v1-in;\n        animation-duration: @time-md;\n        animation-timing-function: @ease-out-quint;\n        &.inverse {\n          opacity: 1;\n          animation-name: anim-furniture-listing-v1-inverse-in;\n          animation-duration: @time-md;\n          animation-timing-function: @ease-out-quint;\n        }\n      }\n      &.fade-out {\n        opacity: 0;\n        animation-name: anim-furniture-listing-v1-inverse-out;\n        animation-duration: @time-md;\n        animation-timing-function: @ease-out-quint;\n        &.inverse {\n          opacity: 0;\n          animation-name: anim-furniture-listing-v1-out;\n          animation-duration: @time-md;\n          animation-timing-function: @ease-out-quint;\n        }\n      }\n    }\n\n    // anim-furniture-listing-v1\n\n    @keyframes anim-furniture-listing-v1-in {\n      0% {\n        opacity: 0;\n        transform: translateY(3rem);\n      }\n      100% {\n        opacity: 1;\n        transform: translateY(0);\n      }\n    }\n\n    @keyframes anim-furniture-listing-v1-out {\n      0% {\n        opacity: 1;\n        transform: translateY(0);\n      }\n      100% {\n        opacity: 0;\n        transform: translateY(3rem);\n      }\n    }\n\n    // anim-furniture-listing-v1-inverse\n\n    @keyframes anim-furniture-listing-v1-inverse-in {\n      0% {\n        opacity: 0;\n        transform: translateY(-3rem);\n      }\n      100% {\n        opacity: 1;\n        transform: translateY(0);\n      }\n    }\n\n    @keyframes anim-furniture-listing-v1-inverse-out {\n      0% {\n        opacity: 1;\n        transform: translateY(0);\n      }\n      100% {\n        opacity: 0;\n        transform: translateY(-3rem);\n      }\n    }\n\n  }\n  /*##END*/\n}\n";