export default "#iframe--fashion-hero-v2 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /* slider */\n\n    .slides-inner {\n      @apply duration-none transform-none !important;\n    }\n\n    .slide {\n      @apply w-full;\n      position: relative;\n      /* activation */\n      display: none;\n      &.active {\n        display: block;\n        z-index: theme('zIndex.active');\n      }\n      &.out {\n        display: block;\n      }\n      &:not(.active) {\n        position: absolute;\n        top: 0;\n        left: 0;\n      }\n    }\n\n    .slider-pagination {\n      display: none;\n    }\n\n    /* hero */\n\n    .hero {\n      @apply w-full;\n      display: block;\n      overflow: hidden;\n    }\n\n    .hero_cover {\n      pointer-events: none;\n      position: absolute;\n      z-index: theme('zIndex.active');\n      top: 0;\n      left: 0;\n      right: 0;\n      bottom: 0;\n      background: theme('colors.accent.500');\n    }\n\n    .hero_asset {\n      .media-container {\n        height: 100vh;\n        height: calc(var(--vh, 1vh) * 100);\n        background: theme('colors.accent.900');\n      }\n    }\n\n    .hero_content {\n      @apply container;\n      position: absolute;\n      z-index: calc(theme('zIndex.active') + 1);\n      top: 0;\n      left: 0;\n      bottom: 0;\n      right: 0;\n      display: flex;\n      align-items: flex-end;\n      justify-content: flex-start;\n      .btn {\n        @apply btn-lg px-0;\n      }\n    }\n\n    .hero_content_inner {\n      @apply block w-full max-w-xl py-10 md:py-16 text-white links-inverse;\n    }\n\n    /* variants */\n\n    .hero--big {\n      .h1 {\n        font-size: 3rem;\n        @screen md {\n          font-size: 5rem;\n        }\n      }\n    }\n\n    .hero--center {\n      .hero_content {\n        justify-content: center;\n        text-align: center;\n      }\n    }\n\n    .hero--middle {\n      @screen md {\n        .hero_content {\n          align-items: center;\n        }\n      }\n    }\n\n    .hero--left {\n      @screen md {\n        .hero_content {\n          justify-content: flex-start;\n          text-align: left;\n        }\n      }\n    }\n\n    .hero--right {\n      @screen md {\n        .hero_content {\n          justify-content: flex-end;\n          text-align: right;\n        }\n      }\n    }\n\n  }\n  /*##END*/\n}\n";