export default "#iframe--brands-featured-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * vars\n      */\n\n    // ease\n\n    @ease-in: @ease-out-quint;\n    @ease-out: @ease-out-quint;\n\n    /**\n      * slider\n      */\n    .slider {\n      .padding(y, medium);\n      .slides-inner {\n        // dragger animations\n        .transition-none() !important;\n      }\n      // .brand\n      .brand {\n        .list();\n        flex-wrap: nowrap;\n      }\n      &.xt-overflow-auto {\n        .slides-inner {\n          justify-content: center;\n        }\n      }\n    }\n\n    // .slider--img\n\n    .slider--img {\n      .slides-inner {\n        .row(medium); // same as .brand\n      }\n      .brand {\n        .list(medium); // same as .slides-inner\n      }\n      .brand_content {\n        .col-custom(300px);\n        // animation\n        transition: transform @time-sm @ease-out;\n        &:hover, &:active, &.active {\n          transform: scale(1.05);\n          transition: transform @time-sm @ease-in;\n        }\n      }\n    }\n\n    // .slider--text\n\n    .slider--text {\n      .slides-inner {\n        .row(giant); // same as .brand\n      }\n      .brand {\n        .list(giant); // same as .slides-inner\n      }\n      .brand_content {\n        .font-sans-semibold();\n        font-size: 12px;\n        color: @text !important;\n        text-transform: uppercase;\n        white-space: nowrap;\n        @media @min-sm {\n          font-size: 1rem;\n        }\n        // animation\n        transition: color @time-sm @ease-out;\n        &:hover, &:active, &.active {\n          color: @accent !important;\n          transition: color @time-sm @ease-in;\n        }\n      }\n      // gradient on left and right\n      &:before {\n        content: '';\n        position: absolute;\n        z-index: 2;\n        top: 0;\n        bottom: 0;\n        left: 0;\n        width: 4rem;\n        background: linear-gradient(90deg, @white 0%, fade(@white, 0%) 100%);\n      }\n      &:after {\n        content: '';\n        position: absolute;\n        z-index: 2;\n        top: 0;\n        bottom: 0;\n        right: 0;\n        width: 4rem;\n        background: linear-gradient(90deg, fade(@white, 0%) 0%, @white 100%);\n      }\n    }\n\n  }\n  /*##END*/\n}\n";